<template>
  <div id="sizeGuideBox">
    <el-container>
      <!-- header -->
      <el-header height="100px">
        <h3 class="el-icon-edit-outline">尺码设置</h3>
        <div class="marginT5">
          <el-button @click="generateJsonData" type="success">生成代码</el-button>
        </div>
      </el-header>

      <!-- 主要内容 -->
      <el-main>
        <el-card shadow="hover">
          <div class="size-table-box">
            <div class="marginB10">
              <el-button icon="el-icon-bottom" type="primary" @click="addNewRow">新增一行</el-button>
              <el-button icon="el-icon-top" type="danger" @click="removeRow">删除一行</el-button>
              <el-button icon="el-icon-right" type="primary" @click="addNewColumn">新增一列</el-button>
              <el-button icon="el-icon-back" type="danger" @click="removeColumn">删除一列</el-button>
              <el-button icon="el-icon-magic-stick" type="primary" @click="pasteSizeGuideCode">自动生成</el-button>
              <el-button icon="el-icon-delete" type="danger" @click="clearSizeGuide">清空数据</el-button>
            </div>
            <el-row type="flex" justify="center" v-for="(rows, x) in treeData" :key="rows.id">
              <el-col :span="22">
                <el-input v-for="(row, y) in rows" :key="row.id" :value="row" v-model="treeData[x][y]" class="size-guide-input"></el-input>
              </el-col>
            </el-row>
          </div>
        </el-card>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "SizeGuideSet",
  data() {
    return {
      treeData: [
        ['Size']
      ],
      rowLen: 1,
      resultJson: {}
    };
  },
  methods: {

    // 添加行
    addNewRow() {
      this.treeData.push(new Array(this.rowLen).fill(''))
    },

    // 删除行
    removeRow(){
      if(this.treeData != null && this.treeData.length > 1) {
        this.treeData.pop()
      }else{
        this.clearSizeGuide()
      }
    },

    // 添加列
    addNewColumn(){
      this.rowLen++
      this.treeData.forEach(t => t.push(''))
    },
    // 删除列
    removeColumn(){
      this.rowLen--
      if(this.rowLen <= 0){
        this.rowLen = 1;
      }
      if(this.treeData != null && this.treeData.length > 1){
        if(this.treeData[0].length == 1){
          this.clearSizeGuide()
        }else{
          this.treeData.forEach(t => t.pop())
        }

      }else{
        this.clearSizeGuide()
      }
    },

    generateJsonData() {
      this.resultJson.sizeGuide = this.treeData
      let inputDom = document.createElement('input')
      inputDom.value = JSON.stringify(this.resultJson)
      document.body.appendChild(inputDom)
      inputDom.select()
      document.execCommand("Copy")
      this.$message({
        message: '代码已经复制到剪切板~',
        type: 'success'
      })
      inputDom.remove()
    },

    // 清空尺码数据
    clearSizeGuide(){
      this.treeData = [['Size']]
      this.rowLen = 1
    },

    // 粘贴代码
    pasteSizeGuideCode(){
      this.$prompt(null, '尺码代码', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(({ value }) => {
          // 读取json数据
          try{
            let treeData = JSON.parse(value)['sizeGuide']
            if(treeData != null && treeData.length > 0){
              this.treeData = treeData
              this.rowLen = treeData[0].length
              this.$message({
                type: 'success',
                message: '生成成功~'
              });
            }else {
              this.$message({
                type: 'error',
                message: '数据异常，请检查粘贴的代码！'
              });
            }
          }catch(err) {
            this.$message({
              type: 'error',
              message: '数据异常，请检查粘贴的代码！'
            });
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消生成'
          });  
        });
    }
  }
}
</script>

<style scoped>
.el-main {
  text-align: center;
}

.size-table-box {
  min-height: 350px;
}

.size-guide-input {
  width: 100px;
  margin-right: 5px;
  margin-bottom: 5px;
}

.marginT10 {
  margin-top: 10px;
}

.marginT5 {
  margin-top: 5px;
}

.marginB5{
  margin-bottom: 5px;
}

.marginB10{
  margin-bottom: 10px;
}

</style>